import { Component, Inject,OnInit } from '@angular/core';
import { MAT_DIALOG_DATA,MatDialogRef } from '@angular/material/dialog';

@Component({
  template: `
    <h4 mat-dialog-title>{{value.title}}</h4>
    <br />
    <div mat-dialog-content>
    <div [innerHTML]="value.data" style="font-size:'larger'"></div> 

    </div>
    <br />
    <div mat-dialog-actions>
      <button mat-button (click)="close()">Close</button>
    </div>
  `
})
export class SimpleDialogComponent  implements OnInit{
  
  description:any;
  constructor(@Inject(MAT_DIALOG_DATA) public value:any,public dialogRef: MatDialogRef<SimpleDialogComponent>) {
  }

  ngOnInit() {
    // will log the entire data object
    console.log(this.value,"model")

  }

  close(): void {
    this.dialogRef.close();
  }
}
