import {
  Component,
  OnInit,
  ViewChild,
  Inject,
  PLATFORM_ID
} from '@angular/core';
import {
  MatDialog,
  MatDialogConfig,
  MatDialogRef
} from '@angular/material/dialog';

import { MapsAPILoader } from '@agm/core';

import { isPlatformBrowser } from '@angular/common';
import { ComplaincecollectionService } from '../Complaince-service.service';
import { SimpleDialogComponent } from './simpleDialog.component';

@Component({
  selector: 'app-Regulation',
  templateUrl: './Regulation.component.html',
  styleUrls: ['./Regulation.component.scss']
})
export class RegulationComponent implements OnInit {
  latitude = 23.00325;
  longitude = 72.66324;
  markers: any;
  simpleDialog: MatDialogRef<SimpleDialogComponent>;
  dialogConfig: MatDialogConfig;

  @ViewChild('streetviewMap', { static: true }) streetviewMap: any;
  @ViewChild('panoramaMap', { static: true }) panoramaMap: any;

  breadscrums = [
    {
      title: 'Regulation',
      items: ['Compliance'],
      active: 'Regulation'
    }
  ];

  // bread crumb items
  constructor(
    @Inject(PLATFORM_ID) private platformId: any,
    private mapsAPILoader: MapsAPILoader,
    private dialogModel: MatDialog,
 
    public ComplaincecollectionService: ComplaincecollectionService
  ) { }
  option3: string = null;
  overallEvalDropDown: any = [];
  tableData:any = null

  ngOnInit(): void {

    if(false) this.dropDownData();
    else{
      alert("Please reach out to contact@sigmared.ai")
      const targetUrl = `${window.location.origin}/#/bias/biasSummary`;
      window.location.href = targetUrl;
    }

  }

  onChange(data: string) {
    this.tableData = null
    this.ComplaincecollectionService.getValue(data)
    .subscribe(incomeing => {
      if(incomeing){
        this.tableData = incomeing
      }
    })
  }

  dialog(data:any,title:any){
    if(this.simpleDialog){
      this.simpleDialog.close()
    }
    
    this.simpleDialog = this.dialogModel.open(SimpleDialogComponent,{
      data: {"data":data,"title":title}
    });
  };

  dropDownData() {
    this.ComplaincecollectionService.dropDown1()
      .subscribe(data => {
        console.log(data?.data, "data?.data", data?.data.length)
        if (data?.data.length) {
          this.overallEvalDropDown = data?.data;
        }
      })
  }

  initStreetViewMap() {
    if (isPlatformBrowser(this.platformId)) {
      this.mapsAPILoader.load().then(() => {
        const center = { lat: 23.02482, lng: 72.55991 };
        const map = new window['google'].maps.Map(
          this.streetviewMap.nativeElement,
          {
            center,
            zoom: 12,
            scrollwheel: false
          }
        );
        const panorama = new window['google'].maps.StreetViewPanorama(
          this.panoramaMap.nativeElement,
          {
            position: center,
            pov: { heading: 34, pitch: 10 },
            scrollwheel: false
          }
        );
        map.setStreetView(panorama);
      });
    }
  }

  private markerData() {
    this.markers = [
      {
        latitude: 23.00325,
        longitude: 72.66324
      },
      {
        latitude: 23.02235,
        longitude: 72.570961
      },
      {
        latitude: 23.01656,
        longitude: 72.5322
      }
    ];
  }
}
